.register-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #141c24;

  .register-content {
    max-width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .bottom-button {
      margin-top: 15px;
    }

    .login-options {
      color: gray;
      display: flex;
      flex-direction: column;
      align-items: center;

      .login-options-title {
        margin-top: 40px;
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-bottom: 10px;

        .span-line {
          width: 100px;
          height: 1px;
          background-color: gray;
          margin: 10px;
        }
      }
    }

    .policy-container {
      display: flex;
      flex-direction: row;
      color: gray;
      margin-top: 20px;
      padding: 0 20px;

      span {
        margin-top: 25px;
        font-size: 18px;
        margin-left: 10px;
        font-family: "Roboto", sans-serif;
      }
    }
  }
}
